* {
  margin: 0;
  padding: 0;
}

:root {
  --grey: #f8f9fa;
}

.quizes {
  display: flex;
  flex-direction: column;
  margin-top: 60px;

}

.card {
  margin: 10px 0px;
}

.card-text {
  margin-bottom: 5px !important;
}

.ct-m {
  margin-right: 20px;
}

.card-text span {
  font-weight: bold;
}

.card-title {
  border-bottom: 1px solid #d2d2d2;
  padding: 6px 0px;
}

.enterQuiz {
  margin-top: 40px;
}

.container-child {
  margin: 0px 20%;
  background: var(--grey);
  padding: 40px;
  border-radius: 6px;
  box-shadow: 2px 2px 4px var(--grey);
}



.enterQuiz div label {
  font-size: 20px;
  margin-left: 8px;

}

.container-child-q{
  margin-bottom: 20px;
}

.fc-enter-q{
  display: flex;
  align-items: center;
}

.quiz-head{
  display: flex;
  justify-content: space-between;
  
}